.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

ul.hr {
  margin: 0;
  padding: 4px; }

ul.hr li {
  display: inline;
  margin-right: 5px;
  border: 1px solid #000;
  padding: 3px; }

div.table-cell {
  margin-left: 10px;
  text-align: center;
  float: left;
  width: 19%; }
  div.table-cell label {
    font-weight: bold; }
  div.table-cell div {
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis; }

div.transaction-content {
  margin-top: 20px;
  min-width: 1000px;
  border: 1px solid #000; }

label.transaction-form-group_amount-label {
  float: left;
  margin-left: 20px; }

label.transaction-form-group_bank-label {
  float: left;
  margin-left: 15px; }

input.transaction-form-group_amount-input {
  float: left;
  margin-left: 30px; }

div.transaction-form-group_bank-select {
  float: left;
  margin-left: 10px; }

div.transaction-form-group_button {
  margin-left: 90px; }

div.clear-class {
  clear: both; }

div.transaction-form-content {
  margin-top: 20px;
  width: 300px;
  border: 1px solid #000; }

div.login-content {
  width: 400px;
  border: 1px solid #000;
  margin-left: 35%;
  margin-top: 10%; }
  div.login-content h2 {
    margin-left: 30%; }

div.login-content_form_form-group-login label {
  margin-left: 10px; }

div.login-content_form_form-group-login input {
  margin-left: 6px; }

div.login-content_form_form-group-password label {
  margin-left: 10px; }

div.login-content_form_form-group-password input {
  margin-left: 10px; }

div.login-content_form_form-group-button button {
  margin-left: 28%; }



